#nav-menu {
  top: 0;
  width: 100%;
  position: fixed;
  text-decoration: "none";
}
#nav-menu-links > *:hover {
  color: #2874f0;
  text-underline-offset: 10px;
  text-decoration-thickness: 3px;
  text-decoration-line: underline;
  text-decoration-color: #2874f0;
}



#home {
  display: flex;
  justify-content: space-between;
  border: 1px solid lightgrey;
}
#home > div {
  border: 1px solid lightgrey;
}
#home > div:nth-child(1) {
  width: 50%;
}
#home > div > img {
  width: 250px;
  height: 250px;
  border-radius: 50%;
  border: 1px solid lightgrey;
}


#about {
  text-align: center;
  border: 1px solid lightgrey;
}


#skills {
  text-align: center;
  border: 1px solid lightgrey;
}
#skills-main {
  width: 90%;
  margin: auto;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 25px;
}
.skills-card {
  display: flex;
  padding: 4px 7px;
  align-items: center;
  border-radius: 4px;
  /* border: 1px solid lightgrey; */
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
}
.skills-card > img {
  height: 45px;
  margin-right: 10px;
}


#contact-main {
  display: flex;
  justify-content: center;
}
#contact-main > a > div {
  display: flex;
  align-items: center;
  flex-direction: column;
}
#contact-main > a:hover {
  text-decoration: none;
}